.block_page .paper .header {
    width: 100%;
    height: 15%;
    background-color: #f7f7f8;
    border-bottom: 1px solid gray
}

.block_page .paper .body {
    height: 70%;
    margin: 0 10%;
    font-size: 1.2rem;
}

.block_page .paper .end {
    border-top: 1px solid gray;
    height: 15%;
    background-color: #f7f7f8;
}

.block_page .paper .end button {
    float: right;
    margin-right: 10px;
    margin-top: 5px;
    padding: 10px;
    cursor: pointer;
    background-color: rgba(46, 229, 157, 0.4);
    border-radius: 10px;
}